<template>
	<div>
		<div class="btn">
			<el-button type="primary" @click="dialogBuy = true">添加购买服务</el-button>
		</div>
		<el-dialog title="添加购买服务" :visible.sync="dialogBuy">
			<div class="box">
				<el-form :model="form" label-width="100px">
					<!-- <el-form-item label="选择学校">
				        <el-select v-model="form.school" placeholder="请选择学校">
				        	<el-option v-for="item in schoolList" :key="item.id" :label="item.title" :value="item.id">
				        	</el-option>
				        </el-select>
				    </el-form-item> -->
					<el-form-item label="商品名称">
						<el-input v-model="form.title"></el-input>
					</el-form-item>
					<el-form-item label="商品价格">
						<el-input v-model="form.price"></el-input>
					</el-form-item>
					<!--    <el-form-item label="商品时长">
				        <el-input v-model="form.timer"></el-input>
				    </el-form-item> -->
					<el-form-item label="商品描述">
						<el-input v-model="form.detail"></el-input>
					</el-form-item>
					<div class="submit">
						<el-form-item>
							<el-button type="primary" @click="newBuy">提交</el-button>
						</el-form-item>
					</div>
				</el-form>
			</div>
		</el-dialog>


		<el-table :data="tableDate">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="商品ID" align="center"></el-table-column>
			<el-table-column prop="title" label="商品名称" align="center"></el-table-column>
			<el-table-column prop="price" label="商品价格" align="center"></el-table-column>
			<el-table-column prop="state" label="商品时长" align="center"></el-table-column>
			<el-table-column prop="detail" label="商品描述" align="center"></el-table-column>
			<el-table-column prop="state" label="服务权限" align="center"></el-table-column>
			<el-table-column prop="created_at" label="创建时间" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button size="mini">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalPage">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import API from '@/api//buy.js'
	
	export default {
		name: 'buy',
		data() {
			return {
				dialogBuy: false,
				form: {
					title: '测试',
					price: '12',
					detail: '测试商品'
				},
				tableDate: [],
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getBuys();
		},
		methods: {
			getBuys() {
				var self = this;
				API.buys(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			newBuy() {
				var self = this;
				API.buy(self.form).then(res => {
					self.dialogBuy = false;
					self.$message.success('提交成功');
					self.getBuys();
					self.currentPage = 1;
					self.form = {};
				})
			},
			
			
			
			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getBuys();
			}
		}
	}
</script>

<style>
</style>
